<script context="module">export { default as BaseChatBot } from "./shared/ChatBot.svelte";
</script>

<script>import ChatBot from "./shared/ChatBot.svelte";
import { Block, BlockLabel } from "@gradio/atoms";
import { Chat } from "@gradio/icons";
import { StatusTracker } from "@gradio/statustracker";
import { normalise_tuples, normalise_messages } from "./shared/utils";
export let elem_id = "";
export let elem_classes = [];
export let visible = true;
export let value = [];
export let scale = null;
export let min_width = void 0;
export let label;
export let show_label = true;
export let root;
export let _selectable = false;
export let likeable = false;
export let show_share_button = false;
export let rtl = false;
export let show_copy_button = true;
export let show_copy_all_button = false;
export let sanitize_html = true;
export let bubble_full_width = true;
export let layout = "bubble";
export let type = "tuples";
export let render_markdown = true;
export let line_breaks = true;
export let latex_delimiters;
export let gradio;
export let avatar_images = [null, null];
let _value = [];
$:
  _value = type === "tuples" ? normalise_tuples(value, root) : normalise_messages(value, root);
export let loading_status = void 0;
export let height = 400;
export let placeholder = null;
export let theme_mode;
</script>

<Block
	{elem_id}
	{elem_classes}
	{visible}
	padding={false}
	{scale}
	{min_width}
	{height}
	allow_overflow={false}
>
	{#if loading_status}
		<StatusTracker
			autoscroll={gradio.autoscroll}
			i18n={gradio.i18n}
			{...loading_status}
			show_progress={loading_status.show_progress === "hidden"
				? "hidden"
				: "minimal"}
			on:clear_status={() => gradio.dispatch("clear_status", loading_status)}
		/>
	{/if}
	<div class="wrapper">
		{#if show_label}
			<BlockLabel
				{show_label}
				Icon={Chat}
				float={true}
				label={label || "Chatbot"}
			/>
		{/if}
		<ChatBot
			i18n={gradio.i18n}
			selectable={_selectable}
			{likeable}
			{show_share_button}
			{show_copy_all_button}
			value={_value}
			{latex_delimiters}
			{render_markdown}
			{theme_mode}
			pending_message={loading_status?.status === "pending"}
			{rtl}
			{show_copy_button}
			on:change={() => gradio.dispatch("change", value)}
			on:select={(e) => gradio.dispatch("select", e.detail)}
			on:like={(e) => gradio.dispatch("like", e.detail)}
			on:share={(e) => gradio.dispatch("share", e.detail)}
			on:error={(e) => gradio.dispatch("error", e.detail)}
			{avatar_images}
			{sanitize_html}
			{bubble_full_width}
			{line_breaks}
			{layout}
			{placeholder}
			upload={gradio.client.upload}
			_fetch={gradio.client.fetch}
			load_component={gradio.load_component}
			msg_format={type}
			root={gradio.root}
		/>
	</div>
</Block>

<style>
	.wrapper {
		display: flex;
		position: relative;
		flex-direction: column;
		align-items: start;
		width: 100%;
		height: 100%;
	}
</style>
